<template>
    <div class="serviceInfo" v-loading="loading" v-if="tableData">
        <div class="serviceInfo-content">
            <el-steps :active="tableData.refundStatus" finish-status="success" simple class="box steps">
                <el-step :title="$L('提交申请')" ></el-step>
                <el-step :title="$L('卖家审核')" ></el-step>
                <el-step :title="$L('填写物料单号')" ></el-step>
                <el-step :title="$L('卖家收货并同意收货')" ></el-step>
            </el-steps>
            <div class="box product-info">
                <div class="title">
                    {{$L('填写物流信息')}}
                    <span>{{$L('申请时间')}}：{{ tableData.applyTime }}</span>
                    <span>{{$L('订单号')}}：{{ tableData.orderSn }}</span>
                    <span>{{$L('退单号')}}：{{ tableData.refundSn }}</span>
                </div>
                <p style="padding-left: 20px;padding-bottom: 20px;font-size: 14px;">{{$L('退款商品')}}</p>
                <div class="info" v-for="(item,key) in tableData.getProductInfo" :key="key">
                    <div class="list-main-left">
                        <img :src="item.smallImg" alt="">
                    </div>
                    <div class="list-main-right">
                        <p class="orderInfo">{{item.chrName}}</p>
                        <p class="gray">{{item.goodsSpec}}</p>
                        <p class="gray">×{{item.goodsNumber}}</p>
                    </div>
                </div>
                <table class="detail">
                    <tr>
                        <td>{{$L('退款金额')}}：</td>
                        <td>
                            <b class="red">{{ tableData.refundAmount }}</b>
                            <span>{{$L('会员账号')}}：{{ tableData.username }}</span>
                            <span>{{$L('买家电话')}}：{{ tableData.mobile }}</span>
                        </td>
                    </tr>
                    <tr>
                        <td>{{$L('退款原因')}}：</td>
                        <td>
                            {{ tableData.refundResult }}

                        </td>
                    </tr>
                    <tr>
                        <td>{{$L('退款附图')}}：</td>
                        <td>
                            <img class="img" :src="i" alt="" v-for="i in tableData.pic">
                        </td>
                    </tr>
                    <tr>
                        <td>{{$L('商家回复')}}：</td>
                        <td>
                            {{ tableData.busMess }}
                        </td>
                    </tr>
                </table>
            </div>

                <div class="title">{{$L('服务单详情')}}</div>
                <el-timeline>
                    <el-timeline-item
                            v-for="(activity, index) in activities"
                            :key="index"
                            :timestamp="activity.date"
                            :type="activity.type"
                            :color="activity.color"
                            :size="activity.size">
                        {{activity.logInfo}}
                    </el-timeline-item>
                </el-timeline>
            <el-form ref="form" :model="form" :rules="applyRules" class="box logistics-info" v-if="tableData.refundStatus === 2">
                <div class="title">{{$L('填写物流信息')}}</div>
                <el-form-item  prop="company">
                    <span>{{$L('物流公司')}}：<el-input size="small" v-model="form.company"></el-input></span>
                </el-form-item>
                <el-form-item  prop="number">
                    <span>{{$L('物流单号')}}：<el-input size="small" v-model="form.number"></el-input></span>
                </el-form-item>
            </el-form>
            <div class="btns">
                <el-button @click="backFunc">{{ $L('返回') }}</el-button>
                <el-button type="primary" @click="submitFunc" v-if="tableData.refundStatus === 2">{{ $L('申请提交') }}</el-button>
                <el-button type="primary" @click="submitTwoFunc" v-if="tableData.refundStatus === 5">{{ $L('再次申请') }}</el-button>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "orderServiceInfo",
        data(){
            return {
                loading:true,
                errorType: 'error',
                errorTitle: this.$L('出错了'),
                errorTips: null,
                errorShow: false,
                form:{
                    company:'',
                    number:''
                },
                applyRules: {
                    number: [
                        {
                            validator: (rule, value, callback) => {
                                value = parseInt(value);
                                if (isNaN(value)) {
                                    callback(new Error(this.$L('请选输入正确的物流单号')));
                                } else {
                                    let patter = /^[0-9a-zA-Z]{15,}$/;
                                    if (!patter.test(value)) {
                                        return callback(new Error(this.$L('物流单号有误')));
                                    } else {
                                        callback();
                                    }

                                }
                            }
                        },
                    ],
                },
                activities: [],
                tableData: {}
            }
        },
        created (){
          this.getData();
        },
        methods:{
            showError(title, tips, type) {
                if (type === 'warning') {
                    this.errorType = 'warning';
                } else {
                    this.errorType = 'error';
                }
                if (title && title !== '') {
                    this.errorTitle = title;
                } else {
                    this.errorTitle = this.$L('出错了');
                }
                if (tips && tips !== '') {
                    this.errorTips = tips;
                } else {
                    this.errorTips = null;
                }
                this.loading = false;
                this.errorShow = true;
            },
            backFunc() {
                this.$router.push({name:'orderList',params:{type:'all'}});
            },
            getData() {
                const _self = this;
                _self.loading = true;
                let id = _self.$route.params.id;
                _self.$api('afterSaleDetail', {orderId: id}, function (reData) {
                    if (reData.flag === 1) {
                        _self.tableData = reData.data.refundOrderInfo;
                        _self.activities =  reData.data.refundLog;
                        for (let i=0;i<_self.activities.length;i++){
                            _self.activities[i].type = 'primary';
                        }
                        _self.loading = false;
                    } else if (reData.flag === -1) {
                        //登录超时了
                        _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                            confirmButtonText: _self.$L('重新登录'),
                            cancelButtonText: _self.$L('取消'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                        }).catch(action => {
                            _self.$gotoWebHome();
                        });
                    } else {
                        //未知错误
                        _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
                            _self.$router.push({name:'orderList',params:{type:'all'}});
                        });
                    }
                }, function (error) {
                    //网络请求错误
                    _self.$confirm(_self.$L('网络请求失败'), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('返回首页'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.getData();
                    }).catch(action => {
                        _self.$router.push({name:'orderList',params:{type:'all'}});
                    });
                });
            },
            submitFunc(){
                const _self = this;
                _self.loading = true;
                if(_self.form.company !== '' && _self.form.number !== ''){
                    let id = _self.tableData.refundId;
                    let arr = {
                        refundId:id,
                        deliveryName: _self.form.company,
                        deliveryNum: _self.form.number
                    };
                    _self.$api('addRefundDelivery', arr, function (reData) {
                        if (reData.flag === 1) {
                            _self.getData();
                            _self.$message({
                                type: 'success',
                                message: _self.$L('提交成功!')
                            });
                            _self.loading = false;
                        } else if (reData.flag === -1) {
                            //登录超时了
                            _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                                confirmButtonText: _self.$L('重新登录'),
                                cancelButtonText: _self.$L('取消'),
                                type: 'error',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                            }).catch(action => {
                                _self.$gotoWebHome();
                            });
                        } else {
                            //未知错误
                            _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                                confirmButtonText: _self.$L('点击重试'),
                                cancelButtonText: _self.$L('返回首页'),
                                type: 'error',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                _self.getData();
                            }).catch(action => {
                                _self.$router.push({name:'orderList',params:{type:'all'}});
                            });
                        }
                    }, function (error) {
                        //网络请求错误
                        _self.$confirm(_self.$L('网络请求失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
                            _self.$router.push({name:'orderList',params:{type:'all'}});
                        });
                    });
                } else {
                    _self.$message({
                        type: 'error',
                        message: _self.$L('物流公司或物流单号不存在!')
                    });
                    _self.loading = false;
                }
            },
            submitTwoFunc(){
                this.$router.push({name: "orderService", params: {id: this.$route.params.id}});
            },
        }
    }
</script>

<style lang="scss" scoped>
    @import "../../../assets/css/common/lib.scss";
    .serviceInfo{
        padding: 20px;
        .serviceInfo-head{
            width: calc(100% - 200px);
            position: fixed;
            top: 50px;
            height: 50px;
            z-index: 999;
            margin: 0 -20px;
            background-color: #fff;
            .left{
                display: inline-block;
                float: left;
                margin-left: 20px;
            }
        }
        .serviceInfo-content{
            background-color: #fff;
            padding: 20px;
            margin: 10px 0;
            .steps{
                font-size: 14px;
            }
            .product-info{
                font-size: 14px;
                .info{
                    position: relative;
                    width: 100%;
                    overflow: hidden;
                    padding-left: 20px;
                    padding-bottom: 20px;
                    &:last-child{
                        padding-bottom: 0;
                    }
                    .list-main-left{
                        width: 100px;
                        height: 100px;
                        float: left;
                        img{
                            width: 100px;
                            height: 100px;
                        }
                    }
                    .list-main-right{
                        padding-left: 18px;
                        float: left;
                        height: 100px;
                        width: calc(100% - 120px);
                        p{
                            line-height: 26px;
                        }
                        .orderInfo{
                            cursor: pointer;
                            white-space: nowrap;
                            text-overflow: ellipsis;
                            overflow: hidden;
                            word-break: break-all;
                            padding: 10px 0;
                            font-size: 14px;
                            @extend .text-one-line;
                        }
                    }
                }
                .detail{
                    width: 100%;
                    font-size: 14px;
                    line-height: 3;
                    padding: 0 20px;
                    display: block;
                    tr:first-child{
                        border-top: 1px solid #ccc;
                        border-bottom: 1px solid #ccc;
                        display: block;
                        line-height: 4;
                    }
                    td{
                        vertical-align: top;
                        span{
                            padding-left: 40px;
                        }
                        .img{
                            width: 60px;
                            height: 60px;
                            margin-right: 10px;
                        }
                    }
                }
            }
            .logistics-info{
                .el-form-item{
                    padding-left: 20px;
                    span:first-child{
                        padding-right: 40px;
                    }
                    .el-input{
                        width: 300px;
                        margin-left: 10px;
                    }
                }

                .el-timeline{
                    padding-left: 20px;
                }
            }
            .btns{
                border-top: 1px solid #ccc;
                padding: 40px 0;
                text-align: right;
                .el-button{
                    width: 100px;
                    height: 38px;
                    line-height: 38px;
                    padding: 0;
                }
            }

            .title{
                font-size: 16px;
                padding-left: 20px;
                height: 50px;
                line-height: 50px;
                background-color: #F2F2F2;
                margin-bottom: 20px;
                span{
                    font-size: 14px;
                    color: #666666;
                    padding-left: 40px;
                }
            }
            .red{
                color: #ff0000;
            }
            .box{
                margin-bottom: 30px;
            }
        }
    }
</style>